
.CycleLabel .section-label {
  display: inline-block;
  float: right;
  padding-right: 20px;
}
.CycleLabel .section-label span {
  display: inline-block;
  border: 1px solid #ccc;
  padding: 0 12px;
  margin: 0 5px;
  border-radius: 2px;
  cursor: pointer;
}
.CycleLabel .section-label .active {
  background: #2c9cde;
  border: 1px solid #2c9cde;
  color: #fff;
}
.CycleLabel .section-label span:hover {
  background: #2c9cde;
  border: 1px solid #2c9cde;
  color: #fff;
}


