
.Login {
  width: 558px;
  height: 400px;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 14px;
  padding-top: 50px;
  position: relative;
}
.Login iframe {
  width: 100%;
  height: 100%;
}
.Login .l-close {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  background: #fff;
}


