
.ListTitle {
  padding: 10px 0;
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
  text-align: left;
}
.ListTitle >.name {
  font-size: 16px;
  color: #e74c3c;
  border-left: 4px solid #e74c3c;
  text-align: left;
  display: inline-block;
  padding-left: 10px;
}
.ListTitle >.desc {
  font-size: 14px;
  color: #a4a4a4;
}
.ListTitle >.btn {
  float: right;
  padding-right: 10px;
  color: #e65742;
}
.ListTitle .am-btn {
  float: right;
  color: #fff;
  background: #5d9cec;
  padding: 0 10px;
  margin-right: 10px;
  border-radius: 4px;
  cursor: pointer;
}


