
.Notice {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #fff;
  border: solid 1px #e5e5e5;
  height: 40px;
  line-height: 40px;
  padding: 0 8px;
  margin-bottom: 10px;
}
.Notice >.not-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #999;
}
.Notice >.not-container >img {
  padding-right: 8px;
}
.Notice >.closebtn {
  height: 26px;
  width: 26px;
  background: #e9e9e9;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
/*overflow hidden*/
}
.Notice >.closebtn:after {
  content: ' ';
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 3px;
  height: 32px;
  background: #b5b5b5;
  -webkit-transform: scale(0.5) rotate(45deg) translate(13px, -20px);
          transform: scale(0.5) rotate(45deg) translate(13px, -20px);
}
.Notice >.closebtn:before {
  content: ' ';
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 3px;
  height: 32px;
  background: #b5b5b5;
  -webkit-transform: scale(0.5) rotate(135deg) translate(13px, 20px);
          transform: scale(0.5) rotate(135deg) translate(13px, 20px);
}


