
.IndexDirectRoomEntrance {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  color: #fff;
  padding: 20px;
  background: #fff;
}
.IndexDirectRoomEntrance >.item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100px;
  box-sizing: border-box;
  margin-bottom: 20px;
  cursor: pointer;
  padding: 10px;
  text-align: left;
  color: #fff !important;
  position: relative;
}
.IndexDirectRoomEntrance >.item img {
  margin-right: 10px;
}
.IndexDirectRoomEntrance >.item p {
  max-width: 160px;
}
.IndexDirectRoomEntrance >.item p a {
  color: #fff;
  text-decoration: underline;
}
.IndexDirectRoomEntrance >.item .wechat {
