}
.IndexDirectRoomEntrance >:nth-child(6n) {
  background: #ef7e83;
  width: calc(33.3333% - 20px);
  margin-right: 20px;
}
.IndexDirectRoomEntrance >:nth-child(6n):hover {
  background: #f18d91;
}


