.wui-confirm-hd {
  width: 100%;
  color: #ee5050;
  height: 50px;
  box-sizing: border-box;
  line-height: 50px;
  border-bottom: 1px solid #f4f4f4;
  position: relative;
}
.wui-confirm-colseBtn {
  position: absolute;
  cursor: pointer;
  background: transparent;
  top: 15px;
  right: 15px;
  height: 20px;
  width: 28px;
  padding: 0;
  margin: 0;
  line-height: 20px;
}
.wui-confirm-bd {
  font-size: 16px;
  padding: 10px 0;
}
.wui-confirm-bd-text {
  padding: 0 50px;
}
