  height: 40px;
  line-height: 40px;
  border: solid #ee5050 1px;
  color: #ee5050;
  background: transparent;
  margin: 0 10px;
  border-radius: 4px;
  cursor: pointer;
  box-sizing: border-box;
}
.wui-confirm-btn:hover {
  color: #fff;
  background: #ee5050;
}


