
body,
ul,
li,
P,
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
a:link {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}
input,
button {
  border: none;
  outline: none;
}
img {
  border: none;
}
.no-select {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.pageContainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.pageContainer li {
  border: 1px solid #e5e5e5;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.pageContainer li a {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  cursor: pointer;
  padding: 5px 10px;
}
.pagination {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.pagination li {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.pagination li a {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  cursor: pointer;
  padding: 5px 10px;
  border: 1px solid #e5e5e5;
  margin: 0 5px;
  text-align: center;
}
.pagination li a:link {
  text-decoration: none;
  color: #000;
}
.pagination li a:visited {
  text-decoration: none;
  color: #000;
}
.pagination li a:hover {
  text-decoration: none;
  color: #000;
}
.pagination li a:active {
  text-decoration: none;
  color: #000;
  outline: none;
}
.pagination .active a {
  color: #0091ff;
  border: 1px solid #0091ff;
}
.menu-header-tips {
  background: #fff;
  padding: 20px 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 240px;
  box-shadow: 0px 0px 1px 1px #b6b6b6, 0px 0px 1px 1px #b6b6b6;
  transition: opacity 0.15s;
  top: 30px !important;
}
.menu-header-tips .tooltip-inner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 240px;
  justify: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.menu-header-tips span {
  font-size: 12px;
  padding: 5px 10px;
  background: #2c9cde;
  margin: 5px;
  border-radius: 5px;
  display: block;
  color: #fff;
}
.menu-header-tips:after,
.menu-header-tips:before {
  border: solid transparent;
  content: ' ';
  height: 0;
  left: 100%;
  position: absolute;
  width: 0;
}
.menu-header-tips:after {
  border-width: 12px;
  border-bottom-color: #fff;
  top: -24px;
  left: 120px;
}
.menu-header-tips:before {
  border-width: 14px;
  border-bottom-color: #b6b6b6;
  top: -28px;
  left: 118px;
}
@import "assets/font/iconfont.css";
#app {
  font-family: Microsoft YaHei;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
/*margin-top: 60px;*/
  background: #f5f5f5;
}


